@charset "utf-8";

.kolonsport-site {
  #kolon-content.main {
    position: relative;
    .visual-slide {
      margin-top: 38px;
      .txt-box {
        position: absolute;
        left: 40px;
        bottom: 51px;
        span {
          display: block;
          color: #000;
        }
        .brand {
          color: #fff;
          height: 16px;
          margin-bottom: 26px;
          &>span {
            background: #000;
            color: #fff;
            float: left;
            font-size: 12px;
            line-height: 16px;
            font-weight: 700;
            margin-right: 2px;
            padding: 0 6px;
          }
        }
        .tit {
          font-size: 30px;
          font-weight: 700;
          line-height: 36px;
          margin-bottom: 13px;
          letter-spacing: -2px;
        }
        .txt {
          font-size: 15px;
          letter-spacing: -.3px;
        }
        &.white {
          span {
            color: #fff;
          }
          .brand>span {
            background: #fff;
            color: #000;
          }
        }
      }
    }
  }
  .main-notice-popup {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    width: 434px;
    min-height: 436px;
    border: 3px solid #000;
    &:not(:first-child) {
      left: 442px;
    }
    a {
      color: #000;
    }
    .content {
      height: 436px;
      &:not(.text) {
        background-size: cover;
        background-position: center;
        h3,
        p {
          display: none;
        }
      }
      &.text {
        height: 344px;
        padding: 46px 56px;
        background: #fff;
        font-size: 14px;
        line-height: 1;
        h3 {
          display: inline-block;
          padding-bottom: 5px;
          margin-bottom: 37px;
          font-weight: bold;
          font-size: 20px;
          border-bottom: 3px solid #1f1f1f;
        }
        strong {
          font-weight: bold;
        }
      }
    }
  }
  .video-wrap {
    position: relative;
    width: 1200px;
    height: 675px;
    margin-top: 50px;
    background-color: #000;
    iframe {
      width: 100% !important;
    }
    .poster {
      background-size: cover;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .video-play {
      position: absolute;
      width: 43px;
      height: 43px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  .thumb-wrap {
    margin-top: 35px;
  }
  .banner {
    a {
      margin-top: 35px;
    }
  }
  .foot {
    padding: 12px 14px;
    background: #000;
    color: #fff;
    .checkbox + label {
      font-size: 14px;
      vertical-align: top;
    }
    .close {
      float: right;
      width: 50px;
      height: 14px;
      background: url('/_ui/img/kolonsport/pc/icn/icn-close-white.png') no-repeat 100% 50%;
      background-size: auto 100%;
      color: #fff;
      font-size: 14px;
      text-align: left;
      vertical-align: top;
      line-height: 14px;
    }
  }
}